<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-button type="primary" size="big" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table border :data="chapterList" highlight-current-row v-loading="listLoading" style="width: 100%;">
			<el-table-column prop="id" label="id" width="80">
			</el-table-column>
			<el-table-column prop="resource_type" label="" width="80">
				<template scope="scope">
					{{ scope.row.resource_type == 0 ? '' : ''  }}
				</template>
			</el-table-column>
			<el-table-column prop="title" label="" width="100">
			</el-table-column>
			<el-table-column prop="desc" label="" width="auto">
			</el-table-column>
			<el-table-column prop="img_url" label="" width="100">
				<template scope="scope">
					<img width="100%" style="vertical-align:middle;" :src="scope.row.img_url" alt="">
				</template>
			</el-table-column>
			<el-table-column label="" width="auto">
				<template scope="scope">
					<el-button type="success" @click.stop="showDetail(scope.row)"></el-button>
				</template>
			</el-table-column>
			<el-table-column label="" width="200">
				<template scope="scope">
					<!--<el-col :span="12">-->
						<!--<el-button class="btn" type="primary" size="small" @click="handleEdit(scope.row)"></el-button>-->
					<!--</el-col>-->
					<el-col :span="18">
						<el-button type="danger" class="btn" size="small" @click="handleDel(scope.row)"></el-button>
					</el-col>
				</template>
			</el-table-column>
		</el-table>
		<el-dialog title="" v-loading.body="addLoading"  v-model="editFormVisible" :close-on-click-modal="false">
			<div class="container">
				<video v-if="editForm.resource_type == 0 && editFormVisible" class="view-cover"
				autoplay="autoplay"
				controls
				:src="editForm.resource.media_url"
				@play="handleEditFormPlay"
				id="my-video2">
				<p></p>
				</video>
				<div v-else class="content">
					<img   class="banner" :src="editForm.resource.img_url" alt="">
					<p>
						{{ editForm.resource.content }}
					</p>
				</div>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-loading.body="addLoading" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="type">
					<el-radio-group v-model="addForm.resource_type">
					    <el-radio :label="0"></el-radio>
					    <el-radio :label="1"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="title">
					<el-input v-model="addForm.title" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label=""  prop="desc">
					<el-input v-model="addForm.desc" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<img v-if="addForm.img_url" class="banner" :src="addForm.img_url" alt="">
					<input type="file" @change="httpUpload($event,'addForm')">
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="addForm.sort"></el-input-number>
				</el-form-item>
				<el-form-item v-if="addForm.resource_type == 0" label="">
					<video v-if="addForm.resource.media_url && addFormVisible" class="view-cover"
						   autoplay="autoplay"
						   controls
						   :src="addForm.resource.media_url"
						   @play="handleAddFormPlay"
					>
						<p></p>
					</video>
					<input type="file" @change="httpVideoUpload($event,'addForm')">
				<!-- 	<el-input v-model='addForm.resource.media_url' placeholder=""></el-input> -->
				</el-form-item>

				<el-form-item v-if="addForm.resource_type == 1" label="">
					<img v-if="addForm.resource.img_url" class="banner" :src="addForm.resource.img_url" alt="">
					<input type="file" @change="httpArticleUpload($event,'addForm')">
				</el-form-item>
				<el-form-item v-if="addForm.resource_type == 1" label="">
					<el-input v-model="addForm.resource.content"  type="textarea" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { uploadFile,uploadVideo,getClassFreeList,addClassFreeList,removeClassFreeList } from '../../api/api';

	export default {
		data() {
			return {
				chapterId:1, // id
				classId:1, // id
				chapterList:[],	
				//
				editForm: {
                    resource_type:0,
                    resource:{
                        resource_id:1,
                        media_url:'',
						img_url:'',
						content:'',
                        media_time:0,
					}
				},

				page: 1,
				listLoading: false,

				editFormVisible: false,//
				editLoading: false,
		
				addFormVisible: false,//
				addLoading: false,
				//
				addForm: {
					class_id: 0,
					img_url:'',
                    resource_type: 0,
                    title:'',
                    sort:'',
					desc: '',
                    resource: {
						resource_id:1,
						media_url:'',
						title:'',
						img_url:'',
						content:'',
						media_time:0
					},
				},
				editFormRules:{},
				addFormRules:{},
			}
		},
		methods: {
            handleEditFormPlay(event){
                let target = event.target;
                this.editForm.resource.media_time = target.duration;
                console.log(this.addForm.resource.media_time);
			},
		    // 
            handleAddFormPlay(event){
                let target = event.target;
                this.addForm.resource.media_time = target.duration;
                console.log(this.addForm.resource.media_time);
			},
            // 
            httpUpload(event,type){
                let file = event.currentTarget.files[0];
                let form = new FormData();
                form.append('file',file);
                uploadFile(form).then( res => {
                    console.log(res);
                    // url
                    this[type].img_url = res.path;
                }).catch( e => {
                    this.$message({
                        message: e,
                        type: 'error'
                    });
                } );
            },
            httpArticleUpload(event,type){
                let file = event.currentTarget.files[0];
                let form = new FormData();
                form.append('file',file);
                this.addLoading = true;
                uploadFile(form).then( res => {
                    this.addLoading = false;
                    // url
                    this[type].resource.img_url = res.path;
                }).catch( e => {
                    this.addLoading = false;
                    this.$message({
                        message: e,
                        type: 'error'
                    });
                } );
            },
            // 
            httpVideoUpload(event,type){

                let file = event.currentTarget.files[0];
                let form = new FormData();
                form.append('file',file);
                this.addLoading = true;
                uploadVideo(form).then( res => {
                    this.addLoading = false;
					// console.log(res);
                    // url
                    this[type].resource.resource_id = res.resource_id;
                    this[type].resource.media_url = res.path;
                }).catch( e => {
                    this.addLoading = false;
                    this.$message({
                        message: e,
                        type: 'error'
                    });
                } );
            },

			getClassChapter(){
				let para = {
					class_id:this.classId
				}
				this.listLoading = true;
				getClassFreeList(para).then( res => {
					this.chapterList = res.list;
					this.listLoading = false;
				} )
			},

			//
			handleDel: function (row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id,classId:this.classId };
					removeClassFreeList(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getClassChapter();
					});
				}).catch(() => {

				});
			},
            showDetail(row){
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
			},
			//
//			handleEdit: function (row) {
//				this.editFormVisible = true;
//				this.editForm = Object.assign({}, row);
//			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
                    class_id: this.classId,
                    img_url:'',
                    resource_type: 0,
                    title:'',
                    sort:'',
                    desc: '',
                    resource: {
                        resource_id:1,
                        media_url:'',
                        title:'',
                        img_url:'',
                        content:'',
						media_time:0,
                    },
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							editClassFreeList(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getClassChapter();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.resource_data = this.addForm.resource;
							addClassFreeList(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getClassChapter();
							});
						});
					}
				});
			},
		    gotoFreeList(row){
		      this.$router.push({path:`/freeList/${row.id}`});
		    },
		},
		mounted() {
			this.classId = this.$route.params.id;
			this.getClassChapter();
		},
	}
</script>

<style scoped>
	.banner-img{
		width:100%;
		height:100%;
		vertical-align:middle;
	}
	.btn{
		margin:5px 0;
	}
	.banner,.view-cover{
		max-width:400px;
		border:1px solid #ccc;
		border-radius:10px;
	}
</style>	


