<template>
	<el-form ref="form" :model="form" label-width="80px" @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
		<el-form-item label="">
			<el-input v-model="form.name"></el-input>
		</el-form-item>
		<el-form-item label="">
			<el-select v-model="form.region" placeholder="">
				<el-option label="" value="shanghai"></el-option>
				<el-option label="" value="beijing"></el-option>
			</el-select>
		</el-form-item>
		<el-form-item label="">
			<el-col :span="11">
				<el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
			</el-col>
			<el-col class="line" :span="2">-</el-col>
			<el-col :span="11">
				<el-time-picker type="fixed-time" placeholder="" v-model="form.date2" style="width: 100%;"></el-time-picker>
			</el-col>
		</el-form-item>
		<el-form-item label="">
			<el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>
		</el-form-item>
		<el-form-item label="">
			<el-checkbox-group v-model="form.type">
				<el-checkbox label="/" name="type"></el-checkbox>
				<el-checkbox label="" name="type"></el-checkbox>
				<el-checkbox label="" name="type"></el-checkbox>
				<el-checkbox label="" name="type"></el-checkbox>
			</el-checkbox-group>
		</el-form-item>
		<el-form-item label="">
			<el-radio-group v-model="form.resource">
				<el-radio label=""></el-radio>
				<el-radio label=""></el-radio>
			</el-radio-group>
		</el-form-item>
		<el-form-item label="">
			<el-input type="textarea" v-model="form.desc"></el-input>
		</el-form-item>
		<el-form-item>
			<el-button type="primary"></el-button>
			<el-button @click.native.prevent></el-button>
		</el-form-item>
	</el-form>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				}
			}
		},
		methods: {
			onSubmit() {
				console.log('submit!');
			}
		}
	}

</script>


