<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
			<el-table-column prop="id" label="id" width="120">
			</el-table-column>
			<el-table-column prop="nickname" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="sex" label="" width="150" :formatter="formatSex" sortable>
				<template scope="scope">
					{{formatSex(scope.row)}}
				</template>
			</el-table-column>
			<el-table-column prop="headimgurl" label="" width="100" sortable>
				<template scope="scope">
					<img class="avatar" :src="scope.row.headimgurl" alt="">
				</template>
			</el-table-column>
			<el-table-column prop="channel_id" label="" width="120">
			</el-table-column>
			<el-table-column label="" width="auto">
				<template scope="scope">
					{{ scope.row.province }} - {{ scope.row.city }}
				</template>
			</el-table-column>
		</el-table>
			
		<!---->
		<el-col :span="24" class="toolbar">
			<!--<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>-->
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getUserList } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//
			}
		},
		methods: {
			//
			formatSex: function (row, column) {
				return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;

                getUserList(para).then((res) => {
					this.total = res.total_page * res.row_num;
					this.users = res.list;
					this.listLoading = false;
				});
			}
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style scoped>
	.avatar{
		vertical-align: middle;
		width:80px;
		height:80px;
		border-radius:50%;
	}
</style>


