import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import ClassList from './views/nav2/classList.vue'
import ClassIndex from './views/nav2/classIndex.vue'
import FreeList from './views/nav2/freeList.vue'
import ClassDetail from './views/nav2/classDetail.vue'
import ClassChapter from './views/nav2/classChapter'
import Page6 from './views/nav3/Page6.vue'
import echarts from './views/charts/echarts.vue'
import BannerList from './views/nav3/bannerList.vue'
import OrderPayList from './views/nav4/orderPayList.vue'
import TestList from './views/nav5/testList.vue'
import TestUserList from './views/nav5/testUserList.vue'
import QuestionList from './views/nav5/questionList.vue'
import AnswerList from './views/nav5/answerList.vue'
let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa el-icon-message',//class
        leaf: true,//
        children: [
            { path: '/table', component: Table, name: '' },
            { path: '/main', component: Main, name: '', hidden: true },
            { path: '/form', component: Form, name: '',hidden: true },
            { path: '/user', component: user, name: '',hidden:true },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        leaf:true,
        children: [
            { path: '/classList', component: ClassList, name: '' },
            { path: '/classIndex/:id', component: ClassIndex, name: '' },
            { path: '/freeList/:id', component: FreeList, name: '' },
            { path: '/classDetail/:id', component: ClassDetail, name: '' },
            { path: '/classChapter/:id/:classId', component: ClassChapter, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: true,//
        children: [
            { path: '/bannerList', component: BannerList, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: true,//
        children: [
            { path: '/orderPayList', component: OrderPayList, name: '' },
            { path: '/page6', component: Page6, name: '',hidden:true }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        children: [
            { path: '/testList', component: TestList, name: '' },
            { path: '/testUserList', component: TestUserList, name: '' },
            { path: '/questionList/:id', component: QuestionList, name: '',hidden:true },
            { path: '/answerList/:id', component: AnswerList, name: '',hidden:true },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/echarts', component: echarts, name: '' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


